*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html{
  background-color: #1b1b32;
}

.App{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato,sans-serif;
}
.primer-contenedor{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}
.logo{
  height:100px;
}
.contenedor-principal{
  height: 500px;
  min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}